@import url(https://fonts.googleapis.com/css?family=Quicksand:300,400,500,700);
@import url(https://fonts.googleapis.com/css?family=Hind:300,400,500,600,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

* {
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  text-align: center; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  display: block; }

h1,
h2,
h3,
h4,
p,
button,
input,
ul,
li {
  font-family: "Quicksand", sans-serif;
  color: #FFFFFF; }

ul li {
  list-style-type: none;
  text-align: center; }

ul {
  margin: 0;
  padding: 0;
  overflow-y: scroll;
  overflow: hidden; }

.App {
  text-align: center;
  background: #20232A;
  background-image: url("https://image.flaticon.com/icons/svg/858/858682.svg");
  background-repeat: no-repeat;
  background-position: bottom right;
  background-size: 25%;
  height: 100vh;
  width: 100vw; }
  .App-header, .App-body {
    height: 25vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0; }
    .App-header__tittle, .App-body__tittle {
      margin: 0; }
    .App-header__form, .App-body__form {
      display: flex;
      flex-direction: column; }
      .App-header__form-input, .App-body__form-input {
        margin: 10px;
        line-height: 2em;
        border-radius: 5px;
        background-color: #20232A;
        border: 0;
        background-image: url(https://image.flaticon.com/icons/svg/2064/2064997.svg);
        background-position: 10px center;
        background-repeat: no-repeat;
        background-size: 10%;
        border: 1px solid #ccc;
        border-radius: 4px;
        padding: 10px 5px 10px 20px;
        text-indent: 20px;
        -webkit-transition: all 0.2s;
        transition: all 0.2s;
        width: 200px; }
      .App-header__form-button, .App-body__form-button {
        margin: 10px;
        line-height: 2em;
        border-radius: 5px;
        border: 0;
        background-color: #FFFFFF;
        color: #20232A; }

